import { AuthState, Client, Project, Payment, Credential } from '@/types';

// Simple encryption using base64 and XOR (for demo purposes - in production use proper encryption)
const ENCRYPTION_KEY = 'mi-clave-secreta-2024';

function simpleEncrypt(text: string): string {
  const key = ENCRYPTION_KEY;
  let encrypted = '';
  for (let i = 0; i < text.length; i++) {
    encrypted += String.fromCharCode(text.charCodeAt(i) ^ key.charCodeAt(i % key.length));
  }
  return btoa(encrypted);
}

function simpleDecrypt(encryptedText: string): string {
  try {
    const key = ENCRYPTION_KEY;
    const encrypted = atob(encryptedText);
    let decrypted = '';
    for (let i = 0; i < encrypted.length; i++) {
      decrypted += String.fromCharCode(encrypted.charCodeAt(i) ^ key.charCodeAt(i % key.length));
    }
    return decrypted;
  } catch {
    return '';
  }
}

// Storage keys
const STORAGE_KEYS = {
  AUTH: 'programmer_auth',
  CLIENTS: 'programmer_clients',
  PROJECTS: 'programmer_projects',
  PAYMENTS: 'programmer_payments',
  CREDENTIALS: 'programmer_credentials',
} as const;

// Auth storage
export const authStorage = {
  save: (authState: AuthState) => {
    localStorage.setItem(STORAGE_KEYS.AUTH, JSON.stringify(authState));
  },
  
  load: (): AuthState | null => {
    try {
      const stored = localStorage.getItem(STORAGE_KEYS.AUTH);
      return stored ? JSON.parse(stored) : null;
    } catch {
      return null;
    }
  },
  
  clear: () => {
    localStorage.removeItem(STORAGE_KEYS.AUTH);
  }
};

// Clients storage
export const clientsStorage = {
  save: (clients: Client[]) => {
    localStorage.setItem(STORAGE_KEYS.CLIENTS, JSON.stringify(clients));
  },
  
  load: (): Client[] => {
    try {
      const stored = localStorage.getItem(STORAGE_KEYS.CLIENTS);
      return stored ? JSON.parse(stored) : [];
    } catch {
      return [];
    }
  },
  
  add: (client: Client) => {
    const clients = clientsStorage.load();
    clients.push(client);
    clientsStorage.save(clients);
  },
  
  update: (id: string, updatedClient: Partial<Client>) => {
    const clients = clientsStorage.load();
    const index = clients.findIndex(c => c.id === id);
    if (index !== -1) {
      clients[index] = { ...clients[index], ...updatedClient, updatedAt: new Date() };
      clientsStorage.save(clients);
    }
  },
  
  delete: (id: string) => {
    const clients = clientsStorage.load();
    const filtered = clients.filter(c => c.id !== id);
    clientsStorage.save(filtered);
  }
};

