// Credentials storage (with encryption)
export const credentialsStorage = {
  save: (credentials: Credential[]) => {
    const encryptedCredentials = credentials.map(cred => ({
      ...cred,
      password: simpleEncrypt(cred.password),
      username: simpleEncrypt(cred.username)
    }));
    localStorage.setItem(STORAGE_KEYS.CREDENTIALS, JSON.stringify(encryptedCredentials));
  },
  
  load: (): Credential[] => {
    try {
      const stored = localStorage.getItem(STORAGE_KEYS.CREDENTIALS);
      if (!stored) return [];
      
      const encryptedCredentials = JSON.parse(stored);
      return encryptedCredentials.map((cred: any) => ({
        ...cred,
        password: simpleDecrypt(cred.password),
        username: simpleDecrypt(cred.username)
      }));
    } catch {
      return [];
    }
  },
  
  add: (credential: Credential) => {
    const credentials = credentialsStorage.load();
    credentials.push(credential);
    credentialsStorage.save(credentials);
  },
  
  update: (id: string, updatedCredential: Partial<Credential>) => {
    const credentials = credentialsStorage.load();
    const index = credentials.findIndex(c => c.id === id);
    if (index !== -1) {
      credentials[index] = { ...credentials[index], ...updatedCredential, updatedAt: new Date() };
      credentialsStorage.save(credentials);
    }
  },
  
  delete: (id: string) => {
    const credentials = credentialsStorage.load();
    const filtered = credentials.filter(c => c.id !== id);
    credentialsStorage.save(filtered);
  }
};

// Utility functions
export const generateId = (): string => {
  return Date.now().toString(36) + Math.random().toString(36).substr(2);
};

export const formatDate = (date: Date | string): string => {
  const d = typeof date === 'string' ? new Date(date) : date;
  return d.toLocaleDateString('es-ES', {
    year: 'numeric',
    month: 'short',
    day: 'numeric'
  });
};

export const formatCurrency = (amount: number, currency: string = 'USD'): string => {
  return new Intl.NumberFormat('es-ES', {
    style: 'currency',
    currency: currency
  }).format(amount);
};
