import { clsx, type ClassValue } from "clsx"
import { twMerge } from "tailwind-merge"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

/**
 * Formats a number as Chilean Pesos (CLP)
 * @param amount - The amount to format
 * @returns Formatted string in CLP format (e.g., "$1.234.567")
 */
export function formatCLP(amount: number): string {
  return new Intl.NumberFormat('es-CL', {
    style: 'currency',
    currency: 'CLP',
    minimumFractionDigits: 0,
    maximumFractionDigits: 0
  }).format(amount);
}

/**
 * Validates if an amount is valid for CLP currency
 * @param amount - The amount to validate
 * @returns boolean indicating if the amount is valid
 */
export function validateCLPAmount(amount: number): boolean {
  return amount >= 0 && amount <= 999999999999; // Max reasonable amount for CLP
}

/**
 * Parses a CLP formatted string back to number
 * @param clpString - The CLP formatted string
 * @returns The numeric value
 */
export function parseCLP(clpString: string): number {
  // Remove currency symbol, spaces, and dots, then parse
  const cleanString = clpString.replace(/[$\s.]/g, '').replace(',', '.');
  return parseFloat(cleanString) || 0;
}

/**
 * Simple encryption function for sensitive data (passwords, etc.)
 * Note: This is a basic implementation. In production, use proper encryption libraries.
 * @param text - Text to encrypt
 * @param key - Encryption key
 * @returns Encrypted string
 */
export function encryptData(text: string, key: string = 'default-key'): string {
  try {
    // Simple XOR encryption (for demo purposes only)
    let encrypted = '';
    for (let i = 0; i < text.length; i++) {
      encrypted += String.fromCharCode(text.charCodeAt(i) ^ key.charCodeAt(i % key.length));
    }
    return btoa(encrypted); // Base64 encode
  } catch (error) {
    console.error('Encryption error:', error);
    return text; // Return original text if encryption fails
  }
}

/**
 * Simple decryption function for sensitive data
 * @param encryptedText - Encrypted text to decrypt
 * @param key - Decryption key
 * @returns Decrypted string
 */
export function decryptData(encryptedText: string, key: string = 'default-key'): string {
  try {
    const encrypted = atob(encryptedText); // Base64 decode
    let decrypted = '';
    for (let i = 0; i < encrypted.length; i++) {
      decrypted += String.fromCharCode(encrypted.charCodeAt(i) ^ key.charCodeAt(i % key.length));
    }
    return decrypted;
  } catch (error) {
    console.error('Decryption error:', error);
    return encryptedText; // Return encrypted text if decryption fails
  }
}
