'use client';

import { useState } from 'react';
import Link from 'next/link';
import { usePathname, useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Sheet, SheetContent, SheetTrigger } from '@/components/ui/sheet';
import { authService } from '@/lib/auth';
import { toast } from 'sonner';
import { cn } from '@/lib/utils';

const navigation = [
  {
    name: 'Dashboard',
    href: '/dashboard',
    description: 'Vista general'
  },
  {
    name: 'Clientes',
    href: '/dashboard/clients',
    description: 'Gestión de clientes'
  },
  {
    name: 'Proyectos',
    href: '/dashboard/projects',
    description: 'Trabajos y proyectos'
  },
  {
    name: 'Pagos',
    href: '/dashboard/payments',
    description: 'Seguimiento de pagos'
  },
  {
    name: 'Credenciales',
    href: '/dashboard/credentials',
    description: 'Accesos y cuentas'
  }
];

interface DashboardNavProps {
  className?: string;
}

export function DashboardNav({ className }: DashboardNavProps) {
  const pathname = usePathname();
  const router = useRouter();
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);

  const handleLogout = () => {
    authService.logout();
    toast.success('Sesión cerrada correctamente');
    router.push('/login');
  };

  const NavItems = () => (
    <>
      {navigation.map((item) => {
        const isActive = pathname === item.href || pathname.startsWith(item.href + '/');
        return (
          <Link
            key={item.name}
            href={item.href}
            className={cn(
              'flex flex-col space-y-1 rounded-lg px-3 py-2 text-sm font-medium transition-colors',
              isActive
                ? 'bg-slate-100 text-slate-900'
                : 'text-slate-600 hover:bg-slate-50 hover:text-slate-900'
            )}
            onClick={() => setIsMobileMenuOpen(false)}
          >
            <span>{item.name}</span>
            <span className="text-xs text-slate-500">{item.description}</span>
          </Link>
        );
      })}
    </>
  );

  return (
    <>
      {/* Desktop Navigation */}
      <nav className={cn('hidden md:flex md:flex-col md:w-64', className)}>
        <div className="flex flex-col h-full bg-white border-r border-slate-200">
          <div className="p-6">
            <h1 className="text-xl font-bold text-slate-900">
              Gestión de Trabajos
            </h1>
            <p className="text-sm text-slate-500 mt-1">
              Panel de programador
            </p>
          </div>
          
          <div className="flex-1 px-4 space-y-1">
            <NavItems />
          </div>
          
          <div className="p-4 border-t border-slate-200">
            <Button
              variant="outline"
              className="w-full"
              onClick={handleLogout}
            >
              Cerrar Sesión
            </Button>
          </div>
        </div>
      </nav>

      {/* Mobile Navigation */}
      <div className="md:hidden">
        <div className="flex items-center justify-between p-4 bg-white border-b border-slate-200">
          <h1 className="text-lg font-semibold text-slate-900">
            Gestión de Trabajos
          </h1>
          
          <Sheet open={isMobileMenuOpen} onOpenChange={setIsMobileMenuOpen}>
            <SheetTrigger asChild>
              <Button variant="outline" size="sm">
                <div className="flex flex-col space-y-1">
                  <div className="w-4 h-0.5 bg-slate-600"></div>
                  <div className="w-4 h-0.5 bg-slate-600"></div>
                  <div className="w-4 h-0.5 bg-slate-600"></div>
                </div>
              </Button>
            </SheetTrigger>
            
            <SheetContent side="left" className="w-64">
              <div className="flex flex-col h-full">
                <div className="py-4">
                  <h2 className="text-lg font-semibold text-slate-900">
                    Menú
                  </h2>
                </div>
                
                <div className="flex-1 space-y-1">
                  <NavItems />
                </div>
                
                <div className="pt-4 border-t border-slate-200">
                  <Button
                    variant="outline"
                    className="w-full"
                    onClick={handleLogout}
                  >
                    Cerrar Sesión
                  </Button>
                </div>
              </div>
            </SheetContent>
          </Sheet>
        </div>
      </div>
    </>
  );
}
