import { useState, useEffect } from 'react';
import { User, AuthState } from '@/types';
import { authStorage, generateId } from './storage';

// Default programmer credentials (in production, this should be configurable)
const DEFAULT_CREDENTIALS = {
  email: 'programador@example.com',
  password: 'admin123',
  name: 'Programador'
};

// Simple password hashing (in production use bcrypt or similar)
function simpleHash(password: string): string {
  let hash = 0;
  for (let i = 0; i < password.length; i++) {
    const char = password.charCodeAt(i);
    hash = ((hash << 5) - hash) + char;
    hash = hash & hash; // Convert to 32bit integer
  }
  return hash.toString();
}

export class AuthService {
  private static instance: AuthService;
  private authState: AuthState = {
    isAuthenticated: false,
    user: null,
    token: null
  };

  private constructor() {
    this.loadAuthState();
  }

  static getInstance(): AuthService {
    if (!AuthService.instance) {
      AuthService.instance = new AuthService();
    }
    return AuthService.instance;
  }

  private loadAuthState(): void {
    const stored = authStorage.load();
    if (stored && this.isValidToken(stored.token)) {
      this.authState = stored;
    }
  }

  private isValidToken(token: string | null): boolean {
    if (!token) return false;
    
    try {
      // Simple token validation (in production use JWT)
      const payload = JSON.parse(atob(token));
      const now = Date.now();
      return payload.exp > now;
    } catch {
      return false;
    }
  }

  private generateToken(user: User): string {
    // Simple token generation (in production use JWT)
    const payload = {
      userId: user.id,
      email: user.email,
      exp: Date.now() + (24 * 60 * 60 * 1000) // 24 hours
    };
    return btoa(JSON.stringify(payload));
  }

  async login(email: string, password: string): Promise<{ success: boolean; error?: string }> {
    try {
      // Validate credentials
      if (email === DEFAULT_CREDENTIALS.email && password === DEFAULT_CREDENTIALS.password) {
        const user: User = {
          id: generateId(),
          email: DEFAULT_CREDENTIALS.email,
          name: DEFAULT_CREDENTIALS.name,
          createdAt: new Date()
        };

        const token = this.generateToken(user);
        
        this.authState = {
          isAuthenticated: true,
          user,
          token
        };

        authStorage.save(this.authState);
        return { success: true };
      } else {
        return { success: false, error: 'Credenciales inválidas' };
      }
    } catch (error) {
      return { success: false, error: 'Error durante el login' };
    }
  }

  logout(): void {
    this.authState = {
      isAuthenticated: false,
      user: null,
      token: null
    };
    authStorage.clear();
  }

  isAuthenticated(): boolean {
    return this.authState.isAuthenticated && this.isValidToken(this.authState.token);
  }

  getCurrentUser(): User | null {
    return this.isAuthenticated() ? this.authState.user : null;
  }

  getToken(): string | null {
    return this.isAuthenticated() ? this.authState.token : null;
  }

  // Method to change password (for future use)
  async changePassword(currentPassword: string, newPassword: string): Promise<{ success: boolean; error?: string }> {
    if (!this.isAuthenticated()) {
      return { success: false, error: 'No autenticado' };
    }

    if (currentPassword !== DEFAULT_CREDENTIALS.password) {
      return { success: false, error: 'Contraseña actual incorrecta' };
    }

    // In a real app, you would update the stored password
    // For now, we'll just return success
    return { success: true };
  }

  // Method to update user profile
  async updateProfile(updates: Partial<User>): Promise<{ success: boolean; error?: string }> {
    if (!this.isAuthenticated() || !this.authState.user) {
      return { success: false, error: 'No autenticado' };
    }

    try {
      this.authState.user = { ...this.authState.user, ...updates };
      authStorage.save(this.authState);
      return { success: true };
    } catch (error) {
      return { success: false, error: 'Error al actualizar perfil' };
    }
  }
}

// Export singleton instance
export const authService = AuthService.getInstance();

// React hook for auth state
export function useAuth() {
  const [authState, setAuthState] = useState<AuthState>({
    isAuthenticated: authService.isAuthenticated(),
    user: authService.getCurrentUser(),
    token: authService.getToken()
  });

  useEffect(() => {
    // Check auth state on mount and set up periodic checks
    const checkAuth = () => {
      setAuthState({
        isAuthenticated: authService.isAuthenticated(),
        user: authService.getCurrentUser(),
        token: authService.getToken()
      });
    };

    checkAuth();
    const interval = setInterval(checkAuth, 60000); // Check every minute

    return () => clearInterval(interval);
  }, []);

  const login = async (email: string, password: string) => {
    const result = await authService.login(email, password);
    if (result.success) {
      setAuthState({
        isAuthenticated: authService.isAuthenticated(),
        user: authService.getCurrentUser(),
        token: authService.getToken()
      });
    }
    return result;
  };

  const logout = () => {
    authService.logout();
    setAuthState({
      isAuthenticated: false,
      user: null,
      token: null
    });
  };

  return {
    ...authState,
    login,
    logout,
    updateProfile: authService.updateProfile.bind(authService),
    changePassword: authService.changePassword.bind(authService)
  };
}
