'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { DashboardNav } from '@/components/dashboard-nav';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { authService } from '@/lib/auth';
import { projectsStorage, clientsStorage } from '@/lib/storage';
import { Project, Client } from '@/types';
import { toast } from 'sonner';

export default function ProjectsPage() {
  const router = useRouter();
  const [projects, setProjects] = useState<Project[]>([]);
  const [clients, setClients] = useState<Client[]>([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    // Check authentication
    if (!authService.isAuthenticated()) {
      router.push('/login');
      return;
    }

    loadProjects();
  }, [router]);

  const loadProjects = () => {
    try {
      const projectsData = projectsStorage.load();
      const clientsData = clientsStorage.load();
      
      // Add client info to projects
      const projectsWithClients = projectsData.map(project => ({
        ...project,
        client: clientsData.find(client => client.id === project.clientId)
      }));

      setProjects(projectsWithClients);
      setClients(clientsData);
    } catch (error) {
      console.error('Error loading projects:', error);
      toast.error('Error al cargar proyectos');
    } finally {
      setIsLoading(false);
    }
  };

  const filteredProjects = projects.filter(project =>
    project.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    project.description?.toLowerCase().includes(searchTerm.toLowerCase()) ||
    project.client?.name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const getStatusBadge = (status: Project['status']) => {
    const statusConfig = {
      'pending': { label: 'Pendiente', variant: 'secondary' as const },
      'in-progress': { label: 'En Progreso', variant: 'default' as const },
      'completed': { label: 'Completado', variant: 'outline' as const },
      'cancelled': { label: 'Cancelado', variant: 'destructive' as const }
    };

    const config = statusConfig[status];
    return (
      <Badge variant={config.variant} className={status === 'completed' ? 'bg-green-100 text-green-800 border-green-200' : ''}>
        {config.label}
      </Badge>
    );
  };

  const formatCLP = (amount: number) => {
    return new Intl.NumberFormat('es-CL', {
      style: 'currency',
      currency: 'CLP',
      minimumFractionDigits: 0,
      maximumFractionDigits: 0
    }).format(amount);
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-slate-200 border-t-slate-600 rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-slate-600">Cargando proyectos...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-slate-50">
      <DashboardNav />
      
      <div className="md:ml-64">
        <div className="p-6">
          {/* Header */}
          <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between mb-8">
            <div>
              <h1 className="text-3xl font-bold text-slate-900">Proyectos</h1>
              <p className="text-slate-600 mt-2">
                Gestiona tus trabajos y proyectos
              </p>
            </div>
            
            <Button 
              onClick={() => toast.info('Funcionalidad próximamente')}
              className="mt-4 sm:mt-0"
            >
              Nuevo Proyecto
            </Button>
          </div>

          {/* Search */}
          <div className="mb-6">
            <Input
              placeholder="Buscar proyectos por título, descripción o cliente..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="max-w-md"
            />
          </div>

          {/* Projects List */}
          {filteredProjects.length === 0 ? (
            <Card>
              <CardContent className="flex flex-col items-center justify-center py-12">
                <div className="w-16 h-16 bg-slate-100 rounded-full flex items-center justify-center mb-4">
                  <div className="w-8 h-8 bg-slate-300 rounded"></div>
                </div>
                <h3 className="text-lg font-semibold text-slate-900 mb-2">
                  {searchTerm ? 'No se encontraron proyectos' : 'No tienes proyectos registrados'}
                </h3>
                <p className="text-slate-500 text-center mb-4">
                  {searchTerm 
                    ? 'Intenta con otros términos de búsqueda'
                    : 'Comienza creando tu primer proyecto'
                  }
                </p>
                {!searchTerm && (
                  <Button onClick={() => toast.info('Funcionalidad próximamente')}>
                    Crear Primer Proyecto
                  </Button>
                )}
              </CardContent>
            </Card>
          ) : (
            <div className="space-y-4">
              <p className="text-sm text-slate-600">
                {filteredProjects.length} proyecto{filteredProjects.length !== 1 ? 's' : ''}
              </p>
              
              <div className="grid gap-4">
                {filteredProjects.map((project) => (
                  <Card key={project.id} className="hover:shadow-md transition-shadow">
                    <CardHeader className="pb-3">
                      <div className="flex items-start justify-between">
                        <div className="flex-1">
                          <CardTitle className="text-lg mb-1">
                            {project.title}
                          </CardTitle>
                          <p className="text-sm text-slate-600">
                            Cliente: {project.client?.name || 'Sin cliente'}
                          </p>
                        </div>
                        <div className="flex items-center space-x-2">
                          {getStatusBadge(project.status)}
                        </div>
                      </div>
                    </CardHeader>
                    
                    <CardContent>
                      {project.description && (
                        <p className="text-slate-600 mb-4 line-clamp-2">
                          {project.description}
                        </p>
                      )}
                      
                      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm">
                        <div>
                          <span className="text-slate-500">Inicio:</span>
                          <p className="font-medium">
                            {new Date(project.startDate).toLocaleDateString('es-CL')}
                          </p>
                        </div>
                        
                        {project.endDate && (
                          <div>
                            <span className="text-slate-500">Fin:</span>
                            <p className="font-medium">
                              {new Date(project.endDate).toLocaleDateString('es-CL')}
                            </p>
                          </div>
                        )}
                        
                        {project.budget && (
                          <div>
                            <span className="text-slate-500">Presupuesto:</span>
                            <p className="font-medium text-green-600">
                              {formatCLP(project.budget)}
                            </p>
                          </div>
                        )}
                        
                        {project.hoursWorked && (
                          <div>
                            <span className="text-slate-500">Horas:</span>
                            <p className="font-medium">
                              {project.hoursWorked}h
                            </p>
                          </div>
                        )}
                      </div>
                      
                      <div className="flex items-center justify-between mt-4 pt-4 border-t border-slate-100">
                        <div className="text-xs text-slate-500">
                          Creado: {new Date(project.createdAt).toLocaleDateString('es-CL')}
                        </div>
                        
                        <div className="flex space-x-2">
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => router.push(`/dashboard/projects/${project.id}`)}
                          >
                            Ver Detalles
                          </Button>
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => toast.info('Funcionalidad próximamente')}
                          >
                            Editar
                          </Button>
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => toast.info('Funcionalidad próximamente')}
                          >
                            Eliminar
                          </Button>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
